import { createStore, combineReducers } from 'redux';
import Data from './data';

const initialTestState = {
  status: 'LOADING',
  height: 0,
  offsetY: 0,
  activeIndex: 0,
  correctAnswers: 0,
  currentQuestion: Data.questions[0],
  planetAnimation: '',
};

const restartTestState = {
  status: 'STARTED',
  height: 0,
  offsetY: 0,
  activeIndex: 0,
  correctAnswers: 0,
  currentQuestion: Data.questions[0],
  planetAnimation: '',
};

const testReducer = function(state = initialTestState, action) {
  switch (action.type) {
    case 'TEST_SET_HEIGHT':
      return Object.assign({}, state, {
        height: window.innerHeight - 50,
      });
    case 'TEST_LOADED':
      return Object.assign({}, state, {
        status: 'LOADED',
      });
    case 'TEST_STARTED':
      document.body.classList.add('mailru-bg--started');
      return Object.assign({}, state, {
        status: 'STARTED'
      });
    case 'TEST_FINISHED':
      document.body.classList.remove('mailru-bg--started');
      return Object.assign({}, state, {
        status: 'FINISHED'
      });
    case 'TEST_SLIDE':
      return Object.assign({}, state, {
        offsetY: action.offsetY,
      });
    case 'TEST_ANSWER':
      return Object.assign({}, state, {
        planetAnimation: '',
        correctAnswers: action.isCorrect ? state.correctAnswers + 1 : state.correctAnswers,
      });
    case 'TEST_CONTINUE':
      return Object.assign({}, state, {
        activeIndex: state.activeIndex + 1,
        planetAnimation: 'planetJump .6s ease-out'
      });

    case 'TEST_RESTART':
      alert(window.innerHeight);
      const height = window.innerHeight - 50;
      document.body.style.background = '#4F5B71';
      document.body.classList.add('mailru-bg--started');
      return Object.assign({}, restartTestState, {
        height: height,
        offsetY: height * 2,
      });
  }

  return state;
};


const reducers = combineReducers({
  testState: testReducer,
});

export default createStore(reducers);